.ai-translate-panel {
  position: fixed;
  top: 130px; /* 탭바(38px) + 메뉴바(48px) + 헤더(44px) = 130px */
  right: 50px; /* 우측 메뉴(40px) + 여백(10px) */
  bottom: 0;
  width: 380px; /* 320px에서 380px로 확대 */
  background: var(--bg-primary);
  border-left: 1px solid var(--border-color);
  box-shadow: -2px 0 8px var(--shadow);
  padding: 16px;
  overflow-y: auto;
  z-index: 1000;
  transition: transform 0.25s cubic-bezier(.4, 1.4, .6, 1), opacity 0.25s cubic-bezier(.4, 1.4, .6, 1);
  transform: translateX(0);
  opacity: 1;
}

.ai-translate-panel.closed {
  transform: translateX(100%);
  opacity: 0;
  pointer-events: none;
}

.sidebar-header {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 8px 8px 16px 0; /* 하단 패딩 증가 */
  margin-bottom: 8px;
  border-bottom: 1px solid var(--border-color); /* 구분선 추가 */
}

.close-panel-btn {
  background: none;
  border: none;
  font-size: 14px;
  cursor: pointer;
  width: 24px;
  height: 24px;
  line-height: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #888;
  border-radius: 4px;
  transition: background 0.15s;
}

.close-panel-btn:hover {
  background: #f0f0f0;
}

.panel-section {
  margin-bottom: 20px;
}

.panel-title {
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 4px;
  color: #333;
}

.subtitle {
  font-size: 13px;
  color: #666;
  margin-bottom: 12px;
}

.divider {
  border: none;
  border-top: 1px solid #eee;
  margin: 12px 0;
}

.language-selector {
  margin-bottom: 16px;
}

.translation-method-selector {
  margin-bottom: 16px;
}

.method-options {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-top: 8px;
}

.method-option {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 12px;
  border: 1px solid #e0e0e0;
  border-radius: 6px;
  cursor: pointer;
  transition: border-color 0.15s, background-color 0.15s;
}

.method-option:hover:not(.disabled) {
  border-color: #007AFF;
  background-color: #f8f9ff;
}

.method-option.disabled {
  opacity: 0.6;
  cursor: not-allowed;
  background-color: #f5f5f5;
}

.method-option input[type="radio"] {
  margin: 0;
}

.method-label {
  font-size: 14px;
  font-weight: 500;
}

.checking {
  color: #666;
  font-size: 12px;
}

.unavailable {
  color: #ff6b6b;
  font-size: 12px;
}

.available {
  color: #28a745;
  font-size: 12px;
}

.method-info {
  margin-top: 8px;
  padding: 8px;
  background-color: #f8f9fa;
  border-radius: 4px;
  border-left: 3px solid #007AFF;
}

.method-info small {
  color: #666;
  font-size: 12px;
}

.selector-label {
  display: block;
  font-size: 13px;
  font-weight: 500;
  color: #333;
  margin-bottom: 8px;
}

.language-dropdown {
  width: 100%;
  padding: 12px 16px;
  border: 1px solid #e0e0e0;
  border-radius: 6px;
  background: var(--bg-primary);
  font-size: 14px;
  color: #333;
  cursor: pointer;
  transition: all 0.15s;
  appearance: none;
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24' fill='none' stroke='currentColor' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'%3e%3cpolyline points='6,9 12,15 18,9'%3e%3c/polyline%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-position: right 12px center;
  background-size: 16px;
  padding-right: 40px;
}

.language-dropdown:hover {
  border-color: #007AFF;
  background-color: #f8f9ff;
}

.language-dropdown:focus {
  outline: none;
  border-color: #007AFF;
  box-shadow: 0 0 0 3px rgba(0, 122, 255, 0.1);
}

.translate-start-btn {
  width: 100%;
  padding: 14px;
  background: #007AFF;
  color: #fff;
  border: none;
  border-radius: 6px;
  font-size: 15px;
  font-weight: 500;
  cursor: pointer;
  transition: background 0.15s;
}

.translate-start-btn:hover:not(:disabled) {
  background: #0056b3;
}

.translate-start-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.translation-progress {
  margin-top: 16px;
}

.progress-bar {
  width: 100%;
  height: 6px;
  background: #f0f0f0;
  border-radius: 3px;
  overflow: hidden;
  margin-bottom: 8px;
}

.progress-fill {
  height: 100%;
  background: linear-gradient(90deg, #007AFF, #0056b3);
  border-radius: 3px;
  transition: width 0.3s ease;
}

.progress-text {
  font-size: 12px;
  color: #666;
  text-align: center;
}

.translation-preview {
  background: #f8f9fa;
  border: 1px solid #e9ecef;
  border-radius: 6px;
  padding: 12px;
  margin-bottom: 8px;
  font-size: 13px;
  line-height: 1.4;
  color: #555;
  max-height: 120px;
  overflow-y: auto;
  white-space: pre-line;
}

.translation-full-content {
  background: #f8f9fa;
  border: 1px solid #e9ecef;
  border-radius: 6px;
  padding: 16px;
  margin-bottom: 8px;
  max-height: 400px;
  overflow-y: auto;
}

.translation-item {
  margin-bottom: 12px;
  padding-bottom: 12px;
  border-bottom: 1px solid #e9ecef;
}

.translation-item:last-child {
  margin-bottom: 0;
  padding-bottom: 0;
  border-bottom: none;
}

.translation-text {
  font-size: 14px;
  line-height: 1.6;
  color: #333;
  white-space: pre-wrap;
  word-break: break-word;
}

.translation-error {
  color: #dc3545;
  font-size: 13px;
  font-style: italic;
  padding: 8px;
  background: #f8d7da;
  border: 1px solid #f5c6cb;
  border-radius: 4px;
  margin-bottom: 8px;
}

.translation-stats {
  font-size: 12px;
  color: #888;
  text-align: center;
  font-weight: 500;
}

.pdf-content-preview {
  background: #f8f9fa;
  border: 1px solid #e9ecef;
  border-radius: 6px;
  padding: 12px;
  margin-bottom: 8px;
  font-size: 12px;
  line-height: 1.4;
  color: #555;
  max-height: 150px;
  overflow-y: auto;
  white-space: pre-wrap;
  word-break: break-word;
}

.content-stats {
  font-size: 11px;
  color: #888;
  text-align: right;
  margin-bottom: 8px;
}

.translate-options {
  margin-top: 16px;
}

.option-item {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 13px;
  color: #333;
  cursor: pointer;
}

.option-item input[type="checkbox"] {
  width: 16px;
  height: 16px;
  cursor: pointer;
}

.info-section {
  margin-top: 24px;
  padding: 16px;
  background: #f8f9fa;
  border-radius: 6px;
}

.info-text {
  font-size: 12px;
  color: #666;
  line-height: 1.4;
  text-align: center;
}
